import React, { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Link } from 'react-router-dom'
import './ProductComponent.css'
import { fetchProducts } from '../../redux/asynProducts/action'

function ProductComponent() {

    const dispatch = useDispatch()
    const style = {
        width: 288
    }
    const products = useSelector(state => state.items)
    const searchedProducts = useSelector(state=>state.productSearch.searchProducts)

    console.log(searchedProducts);

    useEffect(() => {
        dispatch(fetchProducts())
    }, [])

    console.log(products)

    return (
        products.loading ?
            <h1>loading</h1> :
            products.error ?
                <h1>something went wrong</h1> :
                <div className='products'>
                {products.products.filter((item)=>{
                    if(searchedProducts === ''){
                        return item
                    } else if(item.title.toLowerCase().includes(searchedProducts.toLowerCase())){
                        return item
                    }
                })
                   .map((product,val) => (

                        <div className="card" style={style}>

                            <div  className='image'>
                                <Link to={`/product/${product.id}`}>
                                    <img key={product.id}  src={product.image} className="card-img-top product__image" alt="..." />
                                </Link>
                            </div>
                            <div  className="card-body">
                                <h6 key={product.id} className="card-title">{product.title}</h6>
                                <h5 key={val}  className='card-text'>${product.price}</h5>
                                <p   className="card-text">{product.category}</p>
                            </div>
                        </div>


                    ))}
                </div>







    )
}

export default ProductComponent
