import { FETCH_PRODUCT_REQUEST, FETCH_PRODUCT_SUCCESS, FETCH_PRODUCT_FAILURE, SELECTED_PRODUCT, CART_PRODUCTS, REMOVE_PRODUCT, SEARCHED_PRODUCTS } from "./productType"


const initialState = {
    loading: true,
    products: [],
    error: '',
    searchProducts:''
}
let cartState = {
    cart: []
}

const itemReducer = (state = initialState, action) => {
    switch (action.type) {
        case FETCH_PRODUCT_REQUEST: return {
            ...state,
            loading: true
        }
        case FETCH_PRODUCT_SUCCESS: return {
            loading: false,
            products: action.payload,
            error: ''
        }
        case FETCH_PRODUCT_FAILURE: return {
            loading: false,
            products: [],
            error: action.payload
        }
        default: return state
    }

}


export const selectedProductReducer = (state = initialState, action) => {

    switch (action.type) {
        case SELECTED_PRODUCT: return {
            loading: false,
            products: action.payload,
            error: ''
        }
        case REMOVE_PRODUCT: return {
            loading: false,
            products: action.payload,
            error: ''
        }
        default: return state
    }
}

export const cartProductReducer = (state = cartState, action) => {
    switch (action.type) {
        case CART_PRODUCTS: return {
            ...state,
            cartProducts: state.cart.push(action.payload)
        }

        default: return state
    }

}

export const searchProductReducer = (state=initialState,action)=>{
    switch(action.type){
        case SEARCHED_PRODUCTS:return{
             searchProducts:action.payload
        }

        default: return state
    }
}




export default itemReducer