import React,{useState} from 'react'
import './Header.css'
import SearchIcon from '@mui/icons-material/Search';
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart';
import { Link } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import {SearchedProducts} from '../../redux/asynProducts/action'
function Header() {

    


    const cartProducts = useSelector(state => state.cart)
    const searchedProducts = useSelector(state=>state.productSearch.searchProducts)
    const dispatchSearchProducts = useDispatch()


    return (
        <div className='header'>
            <nav className="navbar navbar-expand-lg navbar-light bg-light">
                <Link to='/' className="navbar-brand header__brand " >
                    Fake Shop
                </Link>
                <input type="text" placeholder='search Products..' className='header__search' value={searchedProducts} onChange={(e)=>{dispatchSearchProducts(SearchedProducts(e.target.value)) }} />
                {/* <button onClick={()=>{}}><SearchIcon /></button> */}
                <div className='header__links'>
                    <ul className="nav nav-pills ">
                        <li className="nav-item">
                            <a className="nav-link" href="##">Orders</a>
                        </li>
                    </ul>
                    <div className='header__cart'>
                        <ShoppingCartIcon />
                        <p>{cartProducts.cart.length}</p>
                    </div>

                </div>



            </nav>

        </div>
    )
}

export default Header
