import { FETCH_PRODUCT_FAILURE, FETCH_PRODUCT_REQUEST, FETCH_PRODUCT_SUCCESS, CART_PRODUCTS, SELECTED_PRODUCT, REMOVE_PRODUCT,SEARCHED_PRODUCTS } from "./productType"
import axios from "axios"

export const fetchProductRequest = () => {
    return {
        type: FETCH_PRODUCT_REQUEST
    }
}

export const fetchProductSuccess = (products) => {
    return {
        type: FETCH_PRODUCT_SUCCESS,
        payload: products
    }
}

export const fetchProductFailure = (error) => {
    return {
        type: FETCH_PRODUCT_FAILURE,
        payload: error
    }

}

export const selectedProduct = (product) => {

    return {
        type: SELECTED_PRODUCT,
        payload: product
    }

}
export const removeProduct = () => {
    return {
        type: REMOVE_PRODUCT,
        payload: {}
    }
}

export const cartProducts = (products) => {

    return {
        type: CART_PRODUCTS,
        payload: products
    }

}

export const SearchedProducts = (products)=>{
    return{
        type:SEARCHED_PRODUCTS,
        payload:products
    }
}

export const fetchProducts = () => {
    return (dispatch) => {
        dispatch(fetchProductRequest())
        axios.get('https://fakestoreapi.com/products')
            .then((response) => {
                const products = response.data
                dispatch(fetchProductSuccess(products))
            })
            .catch((error) => {
                const msgerror = error.message
                dispatch(fetchProductFailure(msgerror))
            })
    }
}