import React, { useEffect } from 'react'
import './ProductDetails.css'
import axios from 'axios'
import { useParams } from 'react-router-dom'
import { useSelector, useDispatch } from 'react-redux'
import { cartProducts, removeProduct, selectedProduct } from '../../redux/asynProducts/action'

function ProductDetails() {
    const product = useSelector(state => state.product)
    const cartProduct = useSelector(state => state.cart)
    const { productId } = useParams()
    const dispatch = useDispatch()
    console.log(product)
    const fetchProductDetail = async () => {
        const response = await axios
            .get(`https://fakestoreapi.com/products/${productId}`)
            .then((response) => {
                const product = response.data;
                dispatch(selectedProduct(product))
            })
            .catch((error) => {
                console.log('err', error)
            });
    }

    useEffect(() => {
        if (productId && productId !== 0) {
            fetchProductDetail()
        }
        return () => {
            dispatch(removeProduct(product.products))
        }
    }, [productId])

    console.log(cartProduct)

    return (
        product.loading ?
            <h1>loading</h1> :
            <div className='productDetails__product'>
                <div className='productDetails__imageBox'>
                    <img className='productDetails__image' src={product.products.image} alt="" />
                </div>
                <div className='productDetails__informationBox'>
                    <h2>Description</h2>
                    <p>{product.products.description}</p>
                    <div className='productDetails__info'>
                        <h3>{product.products.title}</h3>
                        <h2 className='productDetails__price'>${product.products.price}</h2>
                    </div>
                    <button className="btn btn-dark" onClick={() => { dispatch(cartProducts(product)) }}>Add to cart</button>
                </div>

            </div>

    )
}

export default ProductDetails
